.body {
  background-image: linear-gradient(to bottom right, gray, #000);
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.watch {
  background-image: linear-gradient(to bottom right, gray, #000);
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 15px #000;
}

.watch__second {
  z-index: 1;
  transform-origin: bottom;
  background-color: red;
  border-radius: 50%;
  width: 2vmin;
  height: 40vmin;
  animation: 5s linear infinite move;
  position: absolute;
  bottom: 50%;
  left: 49%;
  box-shadow: 0 0 5px #000;
}

.watch__minute {
  z-index: 1;
  transform-origin: bottom;
  background-color: #ff0;
  border-radius: 50%;
  width: 4vmin;
  height: 20vmin;
  animation: 60s linear infinite move;
  position: absolute;
  bottom: 50%;
  left: 48%;
  box-shadow: 0 0 5px #000;
}

.watch__circle {
  z-index: 3;
  background-image: linear-gradient(to bottom right, gray, #000);
  border-radius: 50%;
  width: 10vmin;
  height: 10vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 5px #000;
}

